import * as LucideIcon from "lucide-react";
import styled from "styled-components";

import {
	colors, fontFamily, Margins, spacingMap,
} from "../../styleguide";

export type IconName = keyof typeof LucideIcon;

export interface MenuProps {
	icon?: IconName
	action?: () => void
	text?: string
	color?: string
}

export interface StyledCardVariationProps extends Margins {
	hasBackground?: boolean,
	widthSize?: string,
}

export const StyledCardVariation = styled.div<StyledCardVariationProps>`
	border: 1px solid ${colors.lightGray};
	box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.08);
	border-radius: 8px;
	padding: 16px;
	padding-left: ${(props) => props.hasBackground ? 28 : 16}px;
	position: relative;
	width: ${({ widthSize }) => widthSize};

	${(props) => props.mt && { marginTop: `${props.mt}` }}
	${(props) => props.mb && { marginBottom: `${props.mb}` }}
	${(props) => props.mr && { marginRight: `${props.mr}` }}
	${(props) => props.ml && { marginLeft: `${props.ml}` }}
	${(props) => props.m && { margin: `${props.m}` }}
`;

export const BackgroundContainer = styled.div`
	position: absolute;
	max-height: 100%;
	left: 0;
	top: 0;
	overflow: hidden;
	border-radius: 8px 0 0 8px;
`;

export const MenuContainer = styled.div`
	width: 24px;
	height: 100%;
	position: absolute;
	top: 0;
	right: 0;
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	border-left: 1px solid ${colors.lightGray};
	:hover {
			opacity: 0.8;
	}
`;

export const ListContainer = styled.ul`
	position: absolute;
	right: 0;
	z-index: 10;
	list-style-type: none;
	padding: 8px 0;
	background: ${colors.white};
	box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
	border-radius: 8px;
	margin: 0;
	box-sizing: border-box;
`;

export const ListItem = styled.li`
	display: block;
	width: 100%;
	cursor: pointer;
	padding: 0 16px;
	box-sizing: border-box;

	:hover {
			background: ${colors.lighterGray};
	}
`;

export const Item = styled.div`
	display: flex;
	align-items: center;
`;

export const TextItem = styled.span<MenuProps>`
	margin-left: ${spacingMap.sm};
	padding: 9px 0;
	cursor: pointer;
	font-family: ${fontFamily.larsseit};
	font-size: 14px;
  color: ${({ color }) => color};
`;
